$image : '../img';

/*reset*/
* {margin: 0; padding: 0;}
li {list-style:none;}
table{border-collapse: collapse; border-spacing:0; width: 100%;}
.info {
    margin-bottom: 0;
    text-align: left;
}

/* =====================
    common
===================== */
.wrap {width: 1200px; margin: 0 auto;}
.inner {padding: 3% 5%;}
.line {
    padding-top: 6px;
    border-top: 3px solid #333333;
}
.col-2 {
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
    &> * {
        width: calc((100% - 20px) / 2);
    }
}


table {
    border-top: 1px solid #999999;
    th, td {
        padding: 5px 3px;
        border-bottom: 1px solid #999999;
        font-size: 13px;
        text-align: center;
        letter-spacing: -0.7px;
        &:not(:first-child) {border-left: 1px solid #999999;}
    }
    th {
        background-color: #f7f7f7;
        font-weight: 400;
    }
}

/* =====================
    header
===================== */
.header {
    position: relative;
    padding: 15px 10px;
    background-color: #133761;
    .logo {
        position: absolute; top: 50%;
        transform: translateY(-50%);
        img {width: 70px;}
    }
    h1 {
        color: #fff;
        font-size: 18px;
        text-align: center;
    }
}

/* =====================
    wrap
===================== */
.map {
    margin-top: 10px;
    margin-bottom: 26px;
    h2 {
        padding: 8px 0;
        background-color: #575757;
        color: #fff;
        font-size: 16px;
        text-align: center;
    }
    &__con {
        border: 1px solid #dcdcdc;
        border-top: none;
    }
}

.chart {
    &__tit {
        margin-bottom: 3px;
        font-size: 18px;
    }
    &__con {margin-bottom: 10px;}
    &__table {
        tr {
            &.on {
                td {background-color: #ecf2fc;}
            }
        }
        th, td {line-height: 1;}
        td {
            padding: 5px 15px;
            text-align: right;
            &:first-child {text-align: center;}
        }
    }
    h4 {
        margin-bottom: 5px;
        padding: 3px 0;
        background-color: #f7f7f7;
        color: #707070;
        font-size: 15px;
        font-weight: 400;
        text-align: center;
    }
}

.info {
    background-color: #f7f7f7;
    h4 {
        height: 28px; line-height: 28px;
        padding-left: 35px;
        background-image: url(#{$image}/map/img_info.png);
        background-repeat: no-repeat;
        background-size: 28px 26px;
        font-size: 15px;
    }
    ul {
        padding-left: 35px;
    }
    li {
        position: relative;
        padding-left: 5px;
        font-size: 13px;
        &::before {
            content: '';
            position: absolute; top: 6px; left: 0;
            width: 2px; height: 2px;
            border-radius: 50%;
            background-color: #000;
        }
    }
}

/* =====================
    footer
===================== */
.footer {
    padding: 0;
    background-color: #333333;
    &> div {
        display: flex;
        align-items: center;
        padding: 2%;
    }
    .logo {
        width: 100px;
        margin-right: 30px;
        img {width: 100%;}
    }
    p {
        color: #fff;
        font-size: 14px;
        opacity: 0.5;
    }
    a {
        color: #fff;
        opacity: 0.5;
        text-decoration: none;
    }
}